import * as React from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Container from "@mui/material/Container";
import { useLanguageContext } from "../shared/context/LanguageContext";

export default function Portfolio() {
  const {
    language: { portfolioTitle, portfolioDescription, portfolioFix },
  } = useLanguageContext();

  return (
    <React.Fragment>
      <CssBaseline />
      <Container>
        <h1>{portfolioTitle}</h1>
        <p className="home-profile-description">
          {portfolioDescription} <b>{portfolioFix}</b> 🥲
        </p>
      </Container>
      <Container>
        <img
          id="meme"
          src="https://i.gifer.com/7VE.gif"
          alt="John Travolta meme about him being lost"
        />
      </Container>
    </React.Fragment>
  );
}
