import { createTheme } from "@mui/material/styles";

const lightTheme = createTheme({
  palette: {
    mode: "light",
    background: {
      default: "#ececec",
    },
  },
  components: {
    MuiContainer: {
      styleOverrides: {
        root: {
          p: {
            b: {
              color: "#1976d2",
            },
          },
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          boxShadow: "none",
          "&:hover": {
            boxShadow: "0px 0px 20px 0px rgba(0, 0, 0, 0.2) !important",
            transition: "box-shadow 0.3s ease-in-out !important",
          },
        },
      },
    },
  },
});

export default lightTheme;
