import * as React from "react";
import IconButton from "@mui/material/IconButton";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import TwitterIcon from "@mui/icons-material/Twitter";
import GitHubIcon from "@mui/icons-material/GitHub";
import InstagramIcon from "@mui/icons-material/Instagram";
import { Icon } from "@iconify/react";
import Container from "@mui/material/Container";
import { useLanguageContext } from "../context/LanguageContext";
import LanguageDropdown from "../../components/LanguageDropdown";

const Footer = () => {
  const {
    language: { footer },
  } = useLanguageContext();

  return (
    <div>
      <p className="home-footer">{footer}</p>
      <Container className="container-social-media">
        <a
          href="https://www.linkedin.com/in/pablocappetta/"
          target="_blank"
          rel="noreferrer noopener"
          aria-label="LinkedIn"
        >
          <IconButton size="small">
            <LinkedInIcon fontSize="inherit" />
          </IconButton>
        </a>
        <a
          href="https://twitter.com/PabloCappetta"
          target="_blank"
          rel="noreferrer noopener"
          aria-label="Twitter"
        >
          <IconButton size="small">
            <TwitterIcon fontSize="inherit" />
          </IconButton>
        </a>
        <a
          href="https://github.com/pablocappetta"
          target="_blank"
          rel="noreferrer noopener"
          aria-label="GitHub"
        >
          <IconButton size="small">
            <GitHubIcon fontSize="inherit" />
          </IconButton>
        </a>
        <a
          href="https://open.spotify.com/user/11135066385?si=41b13c0b7d064c0d"
          target="_blank"
          rel="noreferrer noopener"
          aria-label="Spotify"
        >
          <IconButton size="small">
            <Icon icon="mdi:spotify" />
          </IconButton>
        </a>
        <a
          href="https://www.instagram.com/pablocappetta/"
          target="_blank"
          rel="noreferrer noopener"
          aria-label="Instagram"
        >
          <IconButton size="small">
            <InstagramIcon fontSize="inherit" />
          </IconButton>
        </a>
        <LanguageDropdown />
      </Container>
    </div>
  );
};

export default Footer;
