import * as React from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import MediaCard from "../components/MediaCard";
import { useLanguageContext } from "../shared/context/LanguageContext";
import ButtonBase from "@mui/material/ButtonBase";

export default function Home() {
  const {
    language: { title, meFix, description, secondDescription, artists },
  } = useLanguageContext();

  return (
    <React.Fragment>
      <CssBaseline />
      <Container className="container-home-page">
        <Box className="box-home-page">
          <Container>
            <h1>{title}</h1>
            <Container className="container-profile-photo-description">
              <p className="home-fix-text home-profile-description">
                {meFix}
                <b>Pablo Cappetta</b>
                {description} 😀
              </p>
            </Container>
          </Container>

          <Container>
            <p className="home-liked-stuff-description">{secondDescription}</p>
            <Container className="container-home-media-cards">
              <a
                href="https://open.spotify.com/artist/0L8ExT028jH3ddEcZwqJJ5?si=jc4UMK7-QAGJkMSZY0Knog"
                target="_blank"
                rel="noreferrer"
              >
                <ButtonBase sx={{ color: "text.primary" }}>
                  <MediaCard
                    img={process.env.PUBLIC_URL + "images/rhcp.webp"}
                    alt={artists[0].alt}
                    title={artists[0].title}
                    description={artists[0].description}
                    className="container-cards"
                  />
                </ButtonBase>
              </a>
              <a
                href="https://open.spotify.com/artist/4tZwfgrHOc3mvqYlEYSvVi?si=ZFm4zFIgSuSgvT3dhkfayw"
                target="_blank"
                rel="noreferrer"
              >
                <ButtonBase sx={{ color: "text.primary" }}>
                  <MediaCard
                    img={process.env.PUBLIC_URL + "images/daftpunk2.jpg"}
                    alt={artists[1].alt}
                    title={artists[1].title}
                    description={artists[1].description}
                  />
                </ButtonBase>
              </a>
              <a
                href="https://open.spotify.com/artist/6J7biCazzYhU3gM9j1wfid?si=aYeogvrzSDeWd8WtF3t9_A "
                target="_blank"
                rel="noreferrer"
              >
                <ButtonBase sx={{ color: "text.primary" }}>
                  <MediaCard
                    img={process.env.PUBLIC_URL + "images/jamiroquai.webp"}
                    alt={artists[2].alt}
                    title={artists[2].title}
                    description={artists[2].description}
                  />
                </ButtonBase>
              </a>
              <a
                href="https://open.spotify.com/artist/2CIMQHirSU0MQqyYHq0eOx?si=D6m7zaj5S5qd5q96BvSVuw"
                target="_blank"
                rel="noreferrer"
              >
                <ButtonBase sx={{ color: "text.primary" }}>
                  <MediaCard
                    img={process.env.PUBLIC_URL + "images/deadmau5.webp"}
                    alt={artists[3].alt}
                    title={artists[3].title}
                    description={artists[3].description}
                  />
                </ButtonBase>
              </a>
            </Container>
          </Container>
        </Box>
      </Container>
    </React.Fragment>
  );
}
