import { createTheme } from "@mui/material/styles";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
  components: {
    MuiContainer: {
      styleOverrides: {
        root: {
          p: {
            b: {
              color: "#90caf9",
            },
          },
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          boxShadow: "none",
          "&:hover": {
            boxShadow: "0px 0px 20px 0px rgba(255, 255, 255, 0.2) !important",
            transition: "box-shadow 0.3s ease-in-out !important",
          },
        },
      },
    },
  },
});

export default darkTheme;
